export class Libs
{
  /**
   * 
   * @returns {string}
   */
  createToken()
  {
    const N = 32;
    return btoa(String.fromCharCode(...crypto.getRandomValues(new Uint8Array(N)))).substring(0,N)
  }

  getNowTime()
  {
    const date = new Date();
    return date.getTime();
  }

  /**
   * 
   * @param {date} oldTime: 
   * @returns {int}
   */
  getDiffTime(oldTime)
  {
    const date = new Date();
    const nowTime = date.getTime();
    const diffTime = nowTime - oldTime;
    return Math.floor(diffTime / (1000));
  }

  /**
   * 
   * @param {HTMLElement} elem 
   * @param {int} min 
   */
  countTextRemove(elem, min)
  {
    let count = min * 1000;
    let clearTimeOut = setTimeout(() => {
      elem.textContent = "";
    }, count);
    
    return clearTimeOut;
  }

  getTimer(mode)
  {
    const week = ['', '', '', '', '',  '', ''] 
    
    // 
    let currentdate = new Date();

    if (mode === 'time') {
      // 
      return setInterval(zeroPadding(currentdate.getHours(), 2) + ':' + zeroPadding(currentdate.getMinutes(), 2) + ':' + zeroPadding(currentdate.getSeconds(), 2), 1000);
    }

    if (mode === 'date') {
      // 
      return setInterval(zeroPadding(currentdate.getFullYear(), 4) + '' + zeroPadding(currentdate.getMonth() + 1, 2) + '' + zeroPadding(currentdate.getDate(), 2) + '' + week[currentdate.getDay()], 1000);
    }

    function zeroPadding(num, len) {
      let zero = '';

     // 0
      for(var i = 0; i < len; i++) {
        zero += '0';
      }

     // zero
      return (zero + num).slice(-len);
    }
  }
}