import { H_BarCodeReader } from "../../src/js/H_barcodeReader";

export default {
  data:function() {
    return {
      date: '',
      time: '',
      week: ['', '', '', '', '',  '', ''] ,
    };
  },
  created() {
    setInterval(this.updateTime, 1000);
  },
  methods: {
    /**
     * 
     */
    updateTime: function() {
      // 
      let currentdate = new Date()

      // 
      this.time = this.zeroPadding(currentdate.getHours(), 2) + ':' + this.zeroPadding(currentdate.getMinutes(), 2) + ':' + this.zeroPadding(currentdate.getSeconds(), 2)

      // 
      this.date = this.zeroPadding(currentdate.getFullYear(), 4) + '' + this.zeroPadding(currentdate.getMonth() + 1, 2) + '' + this.zeroPadding(currentdate.getDate(), 2) + '' + this.week[currentdate.getDay()]
    },
      zeroPadding: function(num, len) {
      let zero = '';

     // 0
      for(var i = 0; i < len; i++) {
        zero += '0';
      }

     // zero
      return (zero + num).slice(-len);
    },

    /**
     * 
     */
    scanStart() {
      try {
        var settingData = require('../../public/barcode.setting.json');
        console.log(settingData);
        const HBarCodeReader = new H_BarCodeReader(settingData);
        HBarCodeReader.startScanner();
      } catch (e) {
        console.log(e); //
        // 
        alert("" + e.message)
      }
    },

    selectStart() {
      console.log(this.code)

    }
  },
}
